<template>
    <div>
        <input type="checkbox" id="nav-toggle" hidden>
	    <nav class="nav">
            <label for="nav-toggle" class="nav-toggle" onclick></label>
            <div class="user-info">
                <span class="data">{{date | date('datetime')}}</span>
                <img src="../assets/duck.png" alt="" width="40%" align="center" hspace="30%" vspace="10%" >
                <div class="input_par">
                    User:{{this.token}}
                </div>
                <div class="input_par">
                    <button class="btn_logout" @click.prevent="logout">Logout</button>
                </div>
            </div>
        </nav>
        <div class="info_dev">
            <div class="info_dev_text">
                <p>The work was performed by Yuri Batalin and Egor Mitrofanov</p>
                Variant 81327
            </div>
		</div>
        <div class="cnv">
            <canvas id="canvas" width="500" height="500" @mousedown="showCoordinates"></canvas>
        </div>
        <form @submit.prevent="submitDataPoints" method="POST">
            <div class="input_param_side">
                <div class="input_side">
                    <div class="input_par">
                        Input cord X:
                    </div>
                    <div class="input_par">
                        <input type="radio" id="x_1" value="-4" v-model="x">-4
                        <input type="radio" id="x_2" value="-3" v-model="x">-3
                        <input type="radio" id="x_3" value="-2" v-model="x">-2
                        <input type="radio" id="x_4" value="-1" v-model="x">-1
                        <input type="radio" id="x0" value="0" v-model="x" checked>0
                        <input type="radio" id="x" value="1" v-model="x">1
                        <input type="radio" id="x2" value="2" v-model="x">2
                        <input type="radio" id="x3" value="3" v-model="x">3
                        <input type="radio" id="x4" value="4" v-model="x">4
                    </div>
                    <div class="input_par">
                        Input cord Y:
                    </div>
                    <div class="input_par">
                        <input class="input" type="number" name="y" id="y" placeholder="Input cord Y" v-model.trim="y" min="-5" max="5" maxlength="8">
                    </div>
                    <div class="input_par">
                        Input R:
                    </div>
                    <div class="input_par">
                        <input type="radio" id="r1" value="1" v-model="r" v-on:click="draw(1)" >
                        <label for="r1">1</label>
                        <input type="radio" id="r2" value="2" v-model="r" v-on:click="draw(2)">
                        <label for="r2">2</label>
                        <input type="radio" id="r3" value="3" v-model="r" v-on:click="draw(3)">
                        <label for="r3">3</label>
                        <input type="radio" id="r4" value="4" v-model="r" v-on:click="draw(4)" checked>
                        <label for="r4">4</label>
                    </div>
                    <div class="input_par">
                        <button class="btn_in_param">Input</button>
                    </div>
                </div>
            </div>
        </form>
        <div class="table-area">
			<div class="input_par">
				<table class="result-table">
					<thead>
						<td>X</td>
						<td>Y</td>
						<td>R</td>
						<td>RESULT</td>
					</thead>
					<tbody>
                        <tr v-for="row in rows" v-bind:key = "row.x">
                            <td>{{row.x}}</td>
                            <td>{{row.y}}</td>
                            <td>{{row.r}}</td>
                            <td>{{row.result}}</td>
                        </tr>
					</tbody>
				</table>
			</div>
		</div>
    </div>
</template>

<script>
import Vue from 'vue'
import axios from 'axios'

export default {
    data: () => ({
        date: new Date(),
        interval: null,
        x: 0,
        y: '',
        r: 4,
        pointsFlag: '0',
        st:'',
        rows: []
    }),
    methods: {
        showCoordinates(e) {
            let rly_x =this.r*((e.offsetX-8 - 250)/200)
            let rly_y = (-1) * this.r*((e.offsetY-8 - 250)/200)
            const DataforPoints = {
                x: rly_x,
                y: rly_y,
                r: this.r,
                pointsFlag: 1,
                token: Vue.prototype.token
            }
            axios.post('http://localhost:8080/rest/main', DataforPoints).then((response) => {
                this.st = response.data
                this.rows = this.st
                this.draw(this.r)
            }).catch(function (error) {
                console.log(error);
            });
        },
        logout() {
            Vue.prototype.token = ''
            this.$router.push('/login?message=logout')
        },
        submitDataPoints(){
            let vm = this
            const DataforPoints = {
                x: vm.x,
                y: vm.y,
                r: vm.r,
                pointsFlag: 1,
                token: Vue.prototype.token
            }
            axios.post('http://localhost:8080/rest/main', DataforPoints).then((response) => {
                this.st = response.data
                this.rows = this.st
                this.draw(this.r)
            }).catch(function (error) {
                console.log(error);
        });
        },
        draw(value){
            let canvas = document.getElementById("canvas");
            let ctx = canvas.getContext("2d");  
            ctx.font = 'bold 15px sans-serif';
            let R = value
            ctx.clearRect(0, 0, 1000, 1000);

            ctx.fillStyle = "rgba(91,95,201,1)";
            ctx.beginPath();
            ctx.moveTo(250, 250);
            //  
            ctx.arc(250, 250, 200, Math.PI, Math.PI/2, true);
            // 
            ctx.moveTo(250, 250);
            ctx.lineTo(50,250);
            ctx.lineTo(50,150);
            ctx.lineTo(250,150);
            ctx.lineTo(250,250);
            // 
            ctx.moveTo(250,250);
            ctx.lineTo(250,450);
            ctx.lineTo(350,250);
            ctx.fill();

            ctx.fillStyle = "rgba(0, 0, 0, 1)";
            ctx.beginPath();
            //  x
            ctx.moveTo(0,250);
            ctx.lineTo(500,250);

            //  x
            ctx.lineTo(485,255);
            ctx.moveTo(500,250);
            ctx.lineTo(485,245);

            //  y
            ctx.moveTo(250,0);
            ctx.lineTo(250,500);
            //  y
            ctx.moveTo(250,0);
            ctx.lineTo(245,15);
            ctx.moveTo(250,0);
            ctx.lineTo(255,15);

            //   X
            ctx.fillText(R/2,350,270);
            ctx.moveTo(350,255);
            ctx.lineTo(350,245);
            ctx.fillText(R,450,270);
            ctx.moveTo(450,255);
            ctx.lineTo(450,245);
            ctx.fillText(-R/2,150,270);
            ctx.moveTo(150,255);
            ctx.lineTo(150,245);
            ctx.fillText(-R,50,270);
            ctx.moveTo(50,255);
            ctx.lineTo(50,245);
            ctx.fillText("X",485,240);

            //   Y
            ctx.fillText(R/2,255,150);
            ctx.moveTo(245,150);
            ctx.lineTo(255,150);
            ctx.fillText(R,255,50);
            ctx.moveTo(245,50);
            ctx.lineTo(255,50);
            ctx.fillText(-R/2,255,350);
            ctx.moveTo(245,350);
            ctx.lineTo(255,350);
            ctx.fillText(-R,255,450);
            ctx.moveTo(245,450);
            ctx.lineTo(255,450);
            ctx.fillText("Y",260,20);

            ctx.closePath();
            ctx.stroke();

            for(let i = 0; i < this.rows.length; i++){
                let x = Number(JSON.stringify(this.rows[i], ['x']).replaceAll('"', "").replace("{","").replace("}","").split(":")[1])
                let y = Number(JSON.stringify(this.rows[i], ['y']).replaceAll('"', "").replace("{","").replace("}","").split(":")[1])
                let res = JSON.stringify(this.rows[i], ['result']).replaceAll('"', "").replace("{","").replace("}","").split(":")[1]
                let flag = x > R || y > R || x < -1 * R || y < -1 * R;
                if(!flag){
                    ctx.beginPath();

                    if (res == "true") {
                        ctx.fillStyle = "rgba(0, 255, 0, 1)";
                    }else{
                        ctx.fillStyle = "rgba(255, 0, 0, 1)";
                    }

                    ctx.arc(250 + 200 * x * 10 / (R*10), 250 - 200 * y*10 / (R*10), 3, 0, 2 * Math.PI);
                    ctx.closePath();
                    ctx.fill();
                }
            }
        }
    },
    mounted() {
        this.draw(this.r)
        this.interval = setInterval(() => {
          this.date = new Date()
        }, 1000)
        if(Vue.prototype.token.trim() == ''){
          this.$router.push('/login')
        }
        const DataforPoints = {
                x: 0,
                y: 0,
                r: 0,
                pointsFlag: 0,
                token: Vue.prototype.token
        }
        axios.post('http://localhost:8080/rest/main', DataforPoints).then((response) => {
                this.st = response.data
                this.rows = this.st
                this.draw(this.r)
            }).catch(function (error) {
                console.log(error);
        });
    },
    beforeDestroy(){
        clearInterval(this.interval)
    }
}
</script>